.calculatorWrapper {
  display: flex;
  justify-content: space-around;
}

.calculatorText {
  display: flex;
  flex-direction: column;
  width: 30%;
  text-align: justify;
}

h2 {
  font-size: 2rem;
  color: #858694;
}

.input-number {
  grid-area: input-number;
}

input {
  width: 100%;
  height: 100%;
  border: none;
  background-color: #858694;
  color: #fff;
  text-align: end;
  padding: 0 10px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.ac {
  grid-area: ac;
}

.percentage {
  grid-area: percentage;
}

.division {
  grid-area: division;
}

.seven {
  grid-area: seven;
}

.eight {
  grid-area: eight;
}

.nine {
  grid-area: nine;
}

.by {
  grid-area: by;
}

.four {
  grid-area: four;
}

.five {
  grid-area: five;
}

.six {
  grid-area: six;
}

.less {
  grid-area: less;
}

.one {
  grid-area: one;
}

.two {
  grid-area: two;
}

.three {
  grid-area: three;
}

.plus {
  grid-area: plus;
}

.zero {
  grid-area: zero;
}

.dot {
  grid-area: dot;
}

.equal {
  grid-area: equal;
}

.grid-container {
  display: grid;
  grid-template-areas:
    "input-number input-number input-number input-number"
    "ac plus-less percentage division"
    "seven eight nine by"
    "four five six less"
    "one two three plus"
    "zero zero dot equal";
  background-color: #bababa;
  width: 600px;
  height: 290px;
  gap: 2px;
}

button {
  width: 100%;
  height: 100%;
  border: none;
  cursor: pointer;
}

.grey-btn {
  background-color: #e0e0e0;
}

.orange-btn {
  background-color: #f5913e;
}
